.t {
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid rgb(230, 206, 126);
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    /* z-index: ;perspective - on top */
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.t-icon {
    width: 15px;
    height: 15px;
}

.t-button {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: rgb(230, 206, 126);
    position: absolute;
    cursor: pointer;
}